<template>
  <v-container
    fluid
    grid-list-md>
    <core-progress-modal :show="loading" />
    <v-layout
      v-if="!loading"
      row
      wrap>
      <v-flex md12>
        <v-form
          novalidate
          @submit.prevent="validateBeforeSubmit">
          <v-card class="elevation-0">
            <v-card-title>
              <h3 class="headline mb-0">Identificação</h3>
            </v-card-title>
            <v-card-text>
              <v-layout
                row
                wrap>
                <v-flex md2>
                  <v-text-field
                    v-validate="'required'"
                    v-model="form.sigla"
                    :error-messages="errors.collect('Sigla')"
                    :disabled="disabled"
                    label="Sigla"
                    data-vv-name="Sigla"
                    required
                  />
                </v-flex>
                <v-flex md4>
                  <v-text-field
                    v-validate="'required'"
                    v-model="form.descricao"
                    :error-messages="errors.collect('Descrição')"
                    :disabled="disabled"
                    label="Descrição"
                    data-vv-name="Descrição"
                    required
                  />
                </v-flex>
                <v-flex md2>
                  <v-text-field
                    v-validate="'required'"
                    v-model="form.qtdParcelas"
                    :error-messages="errors.collect('QtdParcelas')"
                    :disabled="disabled"
                    type="number"
                    min="0"
                    label="QtdParcelas"
                    data-vv-name="QtdParcelas"
                    required
                    reverse
                  />
                </v-flex>
                <v-flex md2>
                  <v-text-field
                    v-validate="'required'"
                    v-formata-moeda="form.valorMinimoParcela"
                    v-money="money"
                    :error-messages="errors.collect('Valor Mínimo Parcela')"
                    v-model.lazy="form.valorMinimoParcela"
                    :disabled="disabled"
                    type="tel"
                    label="Valor Mínimo Parcela"
                    data-vv-name="Valor Mínimo Parcela"
                    suffix="R$"
                    reverse
                  />
                </v-flex>
                <v-flex md2>
                  <v-text-field
                    v-validate="'required'"
                    v-model="form.diasPrimeiraParcela"
                    :disabled="disabled"
                    :error-messages="errors.collect('Dias p/ Primeira Parcela')"
                    type="number"
                    min="0"
                    label="Dias p/ Primeira Parcela"
                    data-vv-name="Dias p/ Primeira Parcela"
                    reverse
                  />
                </v-flex>
              </v-layout>
              <v-layout class="justify-end">
                <v-btn
                  :disabled="disabled"
                  color="primary"
                  @click="gerarParcelas">Definir Parcelas</v-btn>
              </v-layout>
            </v-card-text>
          </v-card>

          <br>

          <template v-if="form.parcelas.length > 0">
            <v-card>
              <v-toolbar
                flat
                color="white">
                <v-toolbar-title>Parcelas do Plano</v-toolbar-title>
                <v-divider
                  class="mx-2"
                  inset
                  vertical />
              </v-toolbar>
              <v-data-table
                :headers="headers"
                :items="form.parcelas"
                :loading="loading"
                hide-actions
                class="elevation-1 arrow-pagintion"
              >
                <template
                  slot="headerCell"
                  slot-scope="{ header }">
                  <span
                    class="subheading font-weight-light text--darken-3"
                    v-text="header.text" />
                </template>
                <template
                  slot="items"
                  slot-scope="{ item }"
                  ma-5
                >
                  <td>{{ item.parcelarEm }}</td>
                  <td>
                    <v-text-field
                      v-formata-moeda="item.peJurosMes"
                      v-money="money"
                      v-model="item.peJurosMes"
                      type="tel"
                      prefix="%"
                      maxlength="5"
                    />
                  </td>
                </template>
              </v-data-table>
            </v-card>
          </template>

          <v-layout class="justify-end">
            <v-btn
              outline
              style="color: black !important"
              @click="voltar">Cancelar</v-btn>
            <v-btn
              :loading="loadingBtn"
              :disabled="acao === 'visualizar'"
              type="submit"
              color="primary"
            >Salvar</v-btn>
          </v-layout>
        </v-form>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { PlanoPagamentoBusiness } from '../../business'
import { MONEY } from '../../constants'

export default {
  metaInfo: {
    titleTemplate: '%s - Plano de Pagamento'
  },
  data () {
    return {
      form: {
        id: null,
        descricao: null,
        sigla: null,
        qtdParcelas: null,
        valorMinimoParcela: 0,
        diasPrimeiraParcela: 30,
        parcelas: []
      },
      money: MONEY,
      headers: [
        { sortable: false, text: 'Parcelar Em', value: 'parcelarEm' },
        { sortable: false, text: '% Juros / mês', value: 'peJurosMes' }
      ],
      loading: false,
      totalItems: 0,
      disabled: false,
      acao: null,
      loadingBtn: false
    }
  },
  beforeMount () {
    let id = this.$route.params.id
    if (id) {
      this.disabled = true
      this.acao = this.$route.params.acao
      this.loading = true
      PlanoPagamentoBusiness.getById(id)
        .then(response => {
          this.form = response.data
          /*
          this.form.valorMinimoParcela = response.data.valorMinimoParcela.toLocaleString('pt-BR', { minimumFractionDigits: 2 })
          this.form.parcelas = response.data.parcelas.map(value => {
            value['peJurosMes'] = value['peJurosMes'].toLocaleString('pt-BR', { minimumFractionDigits: 2 })
            return value
          })
          */
        })
        .finally(() => { this.loading = false })
    }
  },
  methods: {
    validateBeforeSubmit () {
      this.$validator.validate()
        .then(result => {
          if (result) {
            if (this.form.parcelas.length === 0) {
              this.$root.showAlerta('É necessário definir as parcelas antes de salvar')
              return
            }
            this.loadingBtn = true
            PlanoPagamentoBusiness.salvar(this.form)
              .then(response => {
                if (!this.form.id) {
                  this.form = response.data
                  this.$root.showSucesso('Plano de pagamento cadastrado com sucesso.')
                } else {
                  this.$root.showSucesso('Plano de pagamento atualizado com sucesso.')
                }
                this.voltar()
              })
              .catch(error => this.$root.showErro(error.data))
              .finally(() => {
                this.loadingBtn = false
              })
          } else {
            this.$root.showAlerta(this.$vuetify.t('$vuetify.Erro.camposObrigatorios'))
          }
        })
    },
    voltar () {
      this.$router.push('/lista-planopagamento')
    },
    gerarParcelas () {
      if (this.form.qtdParcelas && this.form.qtdParcelas > 0) {
        this.form.parcelas = []
        var i
        for (i = 0; i < this.form.qtdParcelas; i++) {
          this.form.parcelas.push({ parcelarEm: i + 1, peJurosMes: null })
        }
      } else {
        this.$root.showAlerta('Informe a quantidade de parcelas')
      }
    }
  }
}
</script>
